@import "components/bourbon/bourbon";
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900');

@include font-face('SegoeUI', "../fonts/SegoeUI");

$segoe: 'Segoe UI', Helvetica, Arial, sans-serif;
$roboto: 'Roboto', sans-serif;

//  COLORS
$white: #FFFFFF;
$black: #3D3D3D;
$blackText: #707070;
$grey: #969696;
$lightGrey: #EEEEEE;
$orange: #FF7933;
$orange1: #FF891D;
$lightOrange: #FF9100;
$green: #2EC600;
$red: #E60000;

// EXTENDS CLASSES
%transition {
	@include transition(all .4s linear); }
.circle {
	@include border-top-radius(50%);
	@include border-bottom-radius(50%); }
%blockCenter {
	margin-left: auto;
	margin-right: auto; }
%clearfix {
	content: '.';
	display: block;
	clear: both;
	height: 0;
	line-height: 0;
	font-size: 0;
	visibility: hidden;
	overflow: hidden; }

$browser-context: 14; // Default

@function em($pixels, $context: $browser-context) {
	@return #{$pixels/$context}em; }

* {
	padding: 0;
	margin: 0;
	text-decoration: none; }

h1, h2, h3, h4, h5, h6 {
	font-weight: 400; }
h1 {
	font-size: em(36); }
h2 {
	font-size: em(30); }
h3 {
	font-size: em(23); }
h4 {
	font-size: em(21); }
h5 {
	font-size: em(18); }
h6 {
	font-size: em(14); }

html, body {
	height: 100%;
	font-family: $roboto;
	font-size: 14px; }

body {
	overflow: hidden; }

#main {
	height: 100%;
	@include display(flex);
	@include flex-direction(column); }

#content-wrapper {
	flex: 1 1 auto;
	position: relative;
	overflow-y: auto; }

.gridD {
	max-width: 100%;
	@include margin(null 19px);
	position: relative; }

p {
	font-weight: 300;
	color: $blackText;
	line-height: 1.2em; }

small {
	font-size: em(12);
	font-weight: 400; }

.divider-horizontal {
	display: block;
	@include size(100% 1px);
	background: $white; }

.bg-orange-gradient {
	background: url('../img/bg-loader.png') center center no-repeat #FF7933;
	background-size: cover; }

.black-text {
	color: $black; }
.grey-text {
	color: $grey; }

[class*="icon"] {
	color: $orange;
	&.success {
		color: $green; }
	&::before {
		color: inherit;
		display: block; }
	&.rotate180 {
		&::before {
			@include transform(rotate(180deg)); } }
	&.rotate90 {
		&::before {
			@include transform(rotate(90deg)); } } }

.rating {
	@include display(flex);
	@include align-items(center); }

.back-button {
	@include position(fixed, 10px null null 13px);
	a {
		opacity: .54;
		@extend %transition;
		i {
			color: $white; }
		&:hover {
			opacity: .7; } } }

.input-field {
	@include position(relative, null);
	@include padding(6px null); }

.input-field.input-field-icon i,
.input-field.input-field-icon a {
	@include position(absolute, 50% null null 10px);
	@include transform(translateY(-50%)); }

.input-field.input-field-icon a {
	left: auto;
	right: 23px; }

.input-field.input-field-icon input {
	padding-left: 28px; }

.input {
	@include appearance(none);
	border: 1px solid $lightGrey;
	-webkit-border-radius: 6px;
	-moz-border-radius: 6px;
	border-radius: 6px;
	@include size(calc(100% - 28px) 39px);
	font-size: em(14);
	font-weight: 300;
	padding-left: 12px;
	@include placeholder {
		color: $grey; } }

.search-courses {
	position: relative;
	button {
		@include appearance(none);
		border: none;
		@include position(absolute, 50% 17px null null);
		@include transform(translateY(-50%));
		font-size: 24px;
		background: transparent; } }
.input-search {
	@include appearance(none);
	@include border-left-radius(25px);
	@include border-right-radius(25px);
	font-size: 12px;
	padding-left: 42px;
	@include size(calc(100% - 42px) 33px);
	border: none;
	@include placeholder {
		opacity: .4; } }

.btns-center {
	text-align: center; }
.btn {
	@include appearance(none);
	border: none;
	min-width: 213px;
	font-size: 1em;
	line-height: 1em;
	border-radius: 4px;
	text-transform: uppercase;
	color: $white;
	display: block;
	text-align: center;
	@include margin(6px null);
	cursor: pointer;
	@include padding(11px 9px);
	@extend %transition;
	&.btn-orange {
		background: $lightOrange; }
	&.btn-green {
		background: $green; }
	&.btn-grey {
		color: $black;
		background: $lightGrey; }
	&.btn-center {
		@include margin(null auto); } }
a.btn {
	display: inline-block;
	min-width: calc(213px - 18px); }

.checkbox {
	@include size(16px); }

.checkbox-container {
	display: inline-block;
	position: relative;
	padding-left: 25px;
	cursor: pointer;
	@include user-select(none);
	input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		@include size(0); } }

.checkmark {
	@include position(absolute, -1px null null 0);
	width: 14px;
	height: 14px;
	background-color: transparent;
	border: 2px solid $grey;
	@extend %transition; }

.checkbox-container:hover input ~ .checkmark {
	background-color: $lightOrange;
	border-color: $lightOrange; }

.checkbox-container input:checked ~ .checkmark {
	background-color: $green;
	border-color: $green; }

.checkmark:after {
	content: "";
	position: absolute;
	display: none; }

.checkbox-container input:checked ~ .checkmark:after {
	display: block; }

.checkbox-container .checkmark:after {
	@include position(null, -2px null null 4px);
	@include size(6px 13px);
	border: solid white;
	border-width: 0 3px 3px 0;
	@include transform(rotate(45deg)); }

.box-shadow {
	box-shadow: 0 3px 6px rgba(0, 0, 0, .16); }

.bd-radius {
	-webkit-border-radius: 6px;
	-moz-border-radius: 6px;
	border-radius: 6px; }

.progress {
	.row {
		@include display(flex);
		@include justify-content(space-between);
		@include align-items(flex-end);
		p {
			font-size: 12px;
			font-weight: 500;
			color: rgba(0, 0, 0, .54); }
		&.right {
			@include justify-content(flex-end); } } }

.progress-bar {
	@include size(100% 9px);
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	border-radius: 10px;
	display: block;
	position: relative;
	background: rgba($blackText, .29);
	overflow: hidden;
	margin-top: 9px;
	.bar {
		background: $lightOrange;
		height: 100%; } }

.card {
	box-shadow: 0 1px 3px rgba(0, 0, 0, .4);
	border-radius: 4px;
	margin-bottom: 28px;
	margin-top: 20px;
	background: $white;
	overflow: hidden;
	.card-header {
		background: $white;
		@include display(flex);
		@include justify-content(space-between);
		@include padding(9px 16px);
		&.top {
			@include align-items(flex-start); }
		i {
			color: #000000; }
		.card-btn {
			opacity: .54;
			i {
				font-size: 8px;
				&::before {
					@extend %transition; } } }
		.left, .right {
			@include display(flex);
			@include align-items(center); }
		.info {
			margin-left: 12px; }
		.icon {
			@include size(40px);
			background: $lightOrange;
			@extend .circle;
			@include display(flex);
			@include align-items(center);
			@include justify-content(center);
			i {
				font-size: 26px;
				color: $white; } }
		h4 {
			font-size: 14px;
			color: rgba(0, 0, 0, .87); }
		h3 {
			font-size: 14px;
			color: rgba(0, 0, 0, .54); } }

	.card-image {
		@include size(100% 96px);
		display: block;
		background-position: center center;
		position: relative;
		background-size: cover;
		@extend %transition;
		&.big-image {
			height: 291px;
			&::before {
				opacity: .52; } }
		&::before {
			content: '';
			@include position(absolute, 0);
			opacity: 0;
			background: #000000;
			@extend %transition; } }
	.card-resume {
		@include padding(16px);
		border-bottom: 1px solid rgba($black, .49);
		display: none;
		p {
			font-size: em(12); } }
	.card-content {
		background: $lightGrey;
		@include padding(21px);
		display: none;
		&.content-active {
			display: block; } }
	.card-footer {
		@include padding(15px);
		&.buttons {
			@include display(flex);
			@include justify-content(flex-end); }
		a {
			margin-left: 33px;
			color: $orange;
			text-transform: uppercase; } }
	&.card-open {
		.card-btn {
			i {
				&::before {
					@include transform(rotate(0deg)); } } } }
	&.card-profile {
		margin-top: 0;
		position: relative;
		bottom: -20px;
		.icon-bg {
			@include position(null, 16px 16px null null);
			color: $lightOrange;
			opacity: .05;
			font-size: 50px;
			@extend %transition; }
		.card-content {
			background: $white;
			padding-top: 0; }
		.card-header {
			padding-top: 16px;
			.icon-gamification {
				@include size(40px);
				background: $lightOrange;
				-webkit-border-radius: 50%;
				-moz-border-radius: 50%;
				border-radius: 50%;
				@include display(flex);
				@include justify-content(center);
				@include align-items(center);
				margin-right: 12px;
				i {
					color: $white;
					font-size: 21px; } }
			.status {
				p {
					color: #000000;
					font-weight: 500;
					small {
						opacity: .54; } } } }
		&.card-open {
			.icon-bg {
				font-size: 90px; } } } }

.title {
	h3 {
		margin-top: 30px; }
	p {
		font-size: 12px;
		color: $black; } }

#fixed-menu {
	flex: 0 0 auto;
	height: 46px;
	z-index: 10;
	background: $white;
	-webkit-box-shadow: 0 5px 6px rgba(0, 0, 0, .16);
	-moz-box-shadow: 0 5px 6px rgba(0, 0, 0, .16);
	box-shadow: 0 5px 6px rgba(0, 0, 0, .16);
	ul {
		height: 100%;
		@include display(flex);
		@include justify-content(space-around);
		@include align-items(center);
		li {
			@include display(flex);
			a {
				font-size: 27px;
				i {
					color: #BEBEBE; } }
			&.active {
				a {
					i {
						color: $orange; } } } } } }

.icon-bg {
	@include position(absolute, null); }

.banner {
	position: relative;
	margin-bottom: -115px;
	padding-bottom: 130px;
	overflow: hidden;
	background: url('../img/bg-top.jpg') center center no-repeat;
	background-size: cover;
	.icon-bg {
		@include position(null, 40px -18px null null);
		i {
			font-size: 148px;
			color: rgba($white, .2); } }
	.title {
		@include display(flex);
		padding-top: 57px;
		padding-right: 50px;
		.icon {
			margin-right: 10px;
			margin-top: -16px; }
		h6, p {
			color: $white; }
		p {
			margin-top: 15px;
			line-height: 1.5em; }
		i {
			font-size: 39px;
			color: $white; } }
	.courses-area {
		margin-top: 20px; }
	.search {
		margin-top: 23px; }
	&.width-secondary-banner {
		padding-bottom: 25px;
		margin-bottom: 0; } }

.secondary-banner {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	@include padding(40px null 55px);
	h2 {
		font-weight: 700;
		color: $orange1;
		padding-left: 14px;
		padding-right: 90px; }
	p {
		padding-left: 14px;
		padding-right: 90px;
		color: $white; } }

.notification {
	background: $orange1;
	text-align: center;
	@include padding(22px null);
	@include position(absolute, 0 0 null 0);
	z-index: 10;
	span {
		font-size: 13px;
		color: $white; }
	&.success {
		background: $green; }
	&.error {
		background: $red; } }

.course-area {
	display: inline-block;
	a {
		@include display(flex);
		@include align-items(center);
		i {
			color: $black;
			opacity: .16;
			font-size: 37px; }
		span {
			font-size: 12px;
			color: $white;
			margin-left: 11px; }
		&.active {
			i {
				color: $white; } } } }

.slick-dots {
	text-align: center;
	position: absolute;
	li {
		@include size(8px);
		overflow: hidden;
		font-size: 0;
		border: 2px solid $orange;
		display: inline-block;
		margin-right: 8px;
		-webkit-border-radius: 50%;
		-moz-border-radius: 50%;
		border-radius: 50%;
		@extend %transition;
		button {
			@include appearance(none);
			box-shadow: none;
			border: none;
			background: transparent;
			@include size(100%);
			text-indent: -999px;
			cursor: pointer; }
		&.slick-active {
			background: $orange; }
		&:last-child {
			margin-right: 0; } } }

.slider-courses {
	.slick-dots {
		@include position(null, null 0 20px);
		li {
			border-color: $white;
			&.slick-active {
				background: $white; } } } }

.navtabs {
	border-bottom: 1px solid rgba($blackText, .21);
	.gridD {
		@include margin(null 35px); }
	ul {
		@include display(flex);
		@include align-items(center);
		@include justify-content(space-between);
		li {
			height: 54px;
			@include display(flex);
			@include align-items(center);
			@include justify-content(center);
			a {
				color: $blackText; } } }
	.navtabs-bar {
		display: block;
		@include size(20% 3px);
		-webkit-border-radius: 5px;
		-moz-border-radius: 5px;
		border-radius: 5px;
		background: $orange;
		position: relative;
		left: 0;
		@extend %transition; } }

.tab-content {
	position: relative;
	opacity: 0;
	top: 20px;
	display: none;
	&:first-child {
		opacity: 1;
		display: block;
		top: 0; } }

.collection {
	.collection-item {
		border-bottom: 1px solid rgba($blackText, .21);
		@include display(flex);
		@include align-items(center);
		@include justify-content(space-between);
		@include padding(22px 30px 22px 36px);
		.left {
			@include display(flex);
			@include align-items(center); }
		.icon {
			min-width: 22px;
			width: 22px;
			margin-right: 24px;
			i {
				font-size: 23px; }
			&.icon-circle {
				@include size(40px);
				min-width: 40px;
				-webkit-border-radius: 50%;
				-moz-border-radius: 50%;
				border-radius: 50%;
				@include display(flex);
				@include justify-content(center);
				@include align-items(center);
				background-color: #CBCBCB;
				&.success {
					background: $green; }
				&.orange {
					background: $lightOrange; }
				i {
					font-size: 23px;
					color: $white;
					margin-top: -3px; } } }
		p {
			font-weight: 500;
			small {
				opacity: .54; } }
		.info {
			margin-right: 30px; }
		.icon-right {
			i {
				font-size: 10px;
				color: $black;
				opacity: .54; } } } }

.banner-course {
	@include padding(147px null 93px);
	background-size: cover;
	&::before {
		content: '';
		@include position(absolute, 0);
		background: #292929;
		opacity: .41; }
	.gridD {
		position: relative; }
	h1 {
		color: $orange1;
		font-weight: 700; } }
